import React, {useEffect, useState} from "react";
import s from './Content.module.scss'
import Card from "../Card/Card";


const Content = () => {

	const [emoji, setEmoji] = useState([])
	const [data, setData] = useState([])

	const filter = (value) => {
		let newData = data
		value.split(' ').forEach(word => {
			newData = newData.filter(card => card.keywords.toLowerCase().includes(word.toLowerCase()))
		})
		setEmoji(newData)
	}

	useEffect(() => {
		fetch('https://emoji.ymatuhin.workers.dev/')
			.then(resolve => resolve.json())
			.then(data => {
				data.forEach(data => {
					data.keywords = [...new Set(data.keywords.split(' '))].join(' ')
					}
				)
				setData(data)
				setEmoji(data)
			});
	}, [])


	return (

			<section className={s.content}>
				<input
					onChange={(event) => {
						filter(event.target.value)

					}}
					type="text"
					className={s.input}
					placeholder='type keywords for filter'
				/>
				<div className={s.cards}>
					{emoji.map(({title, keywords, symbol}) => (
						<Card key={keywords + title}
						      symbol={symbol}
						      title={title}
						      keywords={keywords}
						/>))}
				</div>

			</section>



	)
}

export default Content